body {
    margin: 0;
    font-family: 'Roboto Mono', monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #5e6472;
    line-height: 1.7;
    font-size: 16px;
    font-weight: 400;
    padding: 0;
    text-align: left;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body h1, h2 {
    font-family: 'Nunito', sans-serif;
}